import axios from '@/libs/api.request'
import {getToken} from '@/libs/util'

// 卡列表
export const getListCard = ( {
                              name,
                              pageNumber,
                              pageSize
                            }) =>  {
  return axios.request( {
    url:'/card/list',
    params: {
      name,
      pageNumber,
      pageSize
    },
    method:'get'
  })
}



// 新增点卡
export const addCard = ( {
                           cardName,
                           description,
                           frequency,
                           contentList,
                         }) =>  {
  return axios.request( {
    url:'/card/add',
    data: {
      cardName,
      description,
      frequency,
      contentList
    },
    method:'post'
  })
}

// 编辑点卡
export const editCard = ( {
                            cardName,
                            description,
                            frequency,
                            contentList,
                            id
                          }) =>  {
  return axios.request( {
    url:'/card/edit',
    data: {
      cardName,
      description,
      frequency,
      contentList,
      id
    },
    method:'post'
  })
}


// 获取点卡
export const getCard = ( {
                           id
                         }) =>  {
  return axios.request( {
    url:'/card/' + id,
    data: {
    },
    method:'get'
  })
}

// 删除点卡
export const delCard = ( {
                           id
                         }) =>  {
  return axios.request( {
    url:'/card/del/' + id,
    method:'get'
  })
}


