// 资产列表
// assetName资产名称； assetNumber资产编号； pageNo页码； pageSize每页显示条数
export const getAssetList = ( {
  isDaycheck,
                               nameOrNum,
                               assetNumber,
                               pageNo,
                               pageSize
                             }) =>  {
  return axios.request( {
    url:'/asset/list',
    params: {
      isDaycheck,
      nameOrNum,
      assetNumber,
      pageNumber:pageNo,
      pageSize
    },
    method:'get'
  })
}



// 新增资产
// assetName设备名称；model型号；assetNumber资产编号；orgId使用部门标识；userId操作人标识；
// integrity完好程度： 1-完好；2-不完好；3-公用；status状态：1-在用；2-闲置；3-停用；4-备用；5-新购；
// serialNumber出厂编号；location存放地点；manufacturer生产商、制造厂家；manufacturer类别标识；useDate使用日期；
// factoryDate出厂年月；amount购买金额；assetPower功率；cardId日检卡标识；remark备注；
export const addAsset = ( {
                           assetName,
                           deviceModelId,
                           assetNumber,
                           orgId,
                           userId,
                           integrity,
                           status,
                           serialNumber,
                           location,
                           manufacturer,
                           deviceTypeId,
                           useDate,
                           factoryDate,
                           amount,
                           assetPower,
                           cardId,
                           isDaycheck,
                           remark
                         }) =>  {
  return axios.request( {
    url:'/asset/add',
    data: {
      assetName,
      deviceModelId,
      assetNumber,
      orgId,
      userId,
      integrity,
      status,
      serialNumber,
      location,
      manufacturer,
      deviceTypeId,
      useDate,
      factoryDate,
      amount,
      assetPower,
      cardId,
      isDaycheck,
      remark
    },
    method:'post'
  })
}

// 编辑资产
export const editAsset = ( {
  deviceModelId,
                            assetNumber,
                            orgId,
                            userId,
                            integrity,
                            status,
                            serialNumber,
                            location,
                            id,
                            assetName,
                            manufacturer,
                            deviceTypeId,
                            useDate,
                            factoryDate,
                            amount,
                            assetPower,
                            cardId,
                            isDaycheck,
                            remark
                          }) =>  {
  return axios.request( {
    url:'/asset/edit',
    data: {
      deviceModelId,
      assetNumber,
      orgId,
      userId,
      integrity,
      status,
      serialNumber,
      location,
      id,
      assetName,
      manufacturer,
      deviceTypeId,
      useDate,
      factoryDate,
      amount,
      assetPower,
      cardId,
      isDaycheck,
      remark
    },
    method:'post'
  })
}

