// 批量导出资产标签
export const labelExport = ({
                              nameOrNum
                            }) =>  {
  window.open(`${axios.baseUrl}/asset/exportLabels?nameOrNum=${nameOrNum}&token=${getToken()}`)
  return Promise.resolve()
};

// 导出单个资产标签
export const labelExport1 = (assetId) =>  {
  window.open(`${axios.baseUrl}/asset/exportLabels?assetId=${assetId}&token=${getToken()}`)
  return Promise.resolve()
};




