<!-- 检点报表 -->
<template>
  <div class="report-info">
    <div class="header-sub">
      <div class="title">
        <div class="breadcrumb-item-link">点检报表</div>
        <span class="empty"></span>
        <span class="t-btn">

        </span>
      </div>
    </div>
    <!--    <div class="header">-->
    <!--      <div class="table-filters">-->
    <!--        <div class="filter-item">-->
    <!--          <span class="label">{{$t_p('asset_label')}}</span>-->
    <!--          <Select class="filter-input" label-in-value v-model="searchName" @on-change="select" filterable-->
    <!--                  :placeholder="$t_p('asset_placeholder')">-->
    <!--            <Option v-for="item in assetList" :value="item.value" :key="item.value">{{ item.label }}</Option>-->
    <!--          </Select>-->
    <!--        </div>-->
    <!--        <div class="filter-item">-->
    <!--          <span>{{$t_p('time_label')}}</span>-->
    <!--          <DatePicker-->
    <!--            v-model="timeRange"-->
    <!--            type="month"-->
    <!--            :placeholder="$t_p('time_placeholder')"-->
    <!--            style="width: 260px;margin-right:20px;"-->
    <!--          ></DatePicker>-->
    <!--        </div>-->
    <!--        <div class="filter-item">-->
    <!--          <Button type="primary" :loading="isQuerying" @click="loadTableData(true)">{{$t('common.message.query')}}-->
    <!--          </Button>-->
    <!--        </div>-->
    <!--      </div>-->
    <!--    </div>-->
    <div class="content" v-show="!isQuerying">
      <div class="filter-search">
        <div class="filter-input">
          <Select class="filter-input" label-in-value v-model="searchName" @on-change="select" filterable
                  :placeholder="$t_p('asset_placeholder')">
            <Option v-for="item in assetList" :value="item.value" :key="item.value">{{ item.label }}</Option>
          </Select>
        </div>
        <div class="filter-input">
          <DatePicker
            v-model="timeRange"
            type="month"
            placeholder="请选择月份"
            style="width: 260px;margin-right:20px;"
          ></DatePicker>
        </div>
        <div class="filter-btn">
          <Button type="primary" @click="loadTableData(true)" :loading="isQuerying">{{$t('common.message.query')}}
          </Button>
        </div>
      </div>
      <div class="title">
        <span>{{assetNumber}}-{{cardName||"--"}}</span>
        <div class="title-right">
          <!--<strong>备注：</strong>-->
          <span class="icon icon-check"></span>{{$t_p('normal_legend')}}
          <span class="icon icon-remove"></span>{{$t_p('op_err_legend')}}
          <span class="icon icon-del"></span>{{$t_p('repair_err_legend')}}
        </div>
      </div>
      <div class="user-table" v-show="tableData.length>0">
        <div class="box">
          <div class="container" style="display: flex;">
            <div class="container-left">
              <ul class="table-left">
                <li class="table-li-1">{{$t_p('index_col')}}</li>
                <li class="table-li-2">{{$t_p('content_col')}}</li>
                <li class="table-li-3">{{$t_p('time_col')}}</li>
              </ul>
              <ul class="table-left" v-for="(item,index) in tableData" v-if="tableData.length>0">
                <li
                  class="table-li-1"
                  :style="{'line-height': 1 * item.opTime.length + 'rem','height':1 * item.opTime.length + 'rem'}"
                >{{item.number}}
                </li>
                <li class="table-li-2" :style="{'height': 1 * item.opTime.length + 'rem'}">
                  <p>{{item.content}}</p>
                </li>
                <li class="table-li-3" :style="{'height': 1 * item.opTime.length + 'rem'}">
                  <ul class="box-time-box">
                    <li v-for="list in item.opTime">
                      {{$t_p(list.time==1?"morning_time":list.time==2?"lunch_time":"night_time")}}
                    </li>
                  </ul>
                </li>
              </ul>
            </div>
            <div class="container-right">
              <ul class="table-right">
                <li v-for="(item,index) in tableColumns || []">{{index+1}}</li>
              </ul>
              <ul class="table-right" v-show="tableData.length>0">
                <li v-for="(item,index) in tableData" class="list-2">
                  <ul v-for="list in item.opTime" class="list-ul">
                    <li
                      :class="i.check==1?'body-icon-check' : i.check==2 ?'body-icon-del' : i.check==3 ? 'body-icon-remove' : 'body-icon'"
                      v-for="i in list.checkList"
                    ></li>
                  </ul>
                </li>
              </ul>
              <ul
                class="table-right"
                style="height:70px;line-height:70px;"
                v-show="tableData.length==0"
              >{{$t_p('no_data')}}
              </ul>
            </div>
          </div>
        </div>
      </div>
    </div>
    <Spin size="large" fix v-if="isQuerying"></Spin>
  </div>
</template>

<script>
  //这里可以导入其他文件（比如：组件，工具js，第三方插件js，json文件，图片文件等等）
  import moment from 'moment'
  import CommonLoading from '@c/common-loading'
  import AssetSelect from '@c/asset-select'
  import {getAssetList, getReport} from '@/api/asset'

  export default {
    //import引入的组件需要注入到对象中才能使用
    components: {CommonLoading, AssetSelect},
    i18n_prefix: "page.report-info",
    data() {
      //这里存放数据
      return {
        cardName: "",
        searchName: "",
        assetList: [],
        name: "",
        assetNumber: "",
        timeRange: moment().format("YYYY-MM"),
        searchOp: "",
        opList: [],
        reportList: [],
        marginLeft: 0,
        num: 31,
        pre: 1,
        tableColumns: [
          // {
          //   type: "index",
          //   title: this.$t_p('index_col'),
          //   align: "center",
          //   width: 80,
          //   fixed: "left"
          // },
          // {
          //   title:  this.$t_p('content_col'),
          //   key: "content",
          //   width: 130,
          //   align: "center",
          //   fixed: "left"
          // },
          // {
          //   title: this.$t_p('time_col'),
          //   key: "opTime",
          //   width: 150,
          //   align: "center",
          //   fixed: "left"
          // }
        ],
        tableData: [],
        pageInfo: {
          pageIndex: 1,
          pageSize: 10,
          totalElements: 0
        },
        items: [],
        isQuerying: false
      };
    },
    //监听属性 类似于data概念
    computed: {},
    //监控data中的数据变化
    watch: {},
    //方法集合
    methods: {
      select(val) {
        // console.log(val)
        // this.searchName = val.id;
        this.name = val.label;
        // if(this.searchName){
        //   this.loadTableData()
        // }

      },
      getType() {
        getAssetList({
          isDaycheck: 1
        }).then(res => {
          if (res.data.result.length > 0) {
            this.assetList = (res.data.result || []).map((d) => {
              return {
                value: d.id + '',
                label: `${d.assetName}`
              }
            });
            if (this.assetList.length > 0) {
              this.searchName = this.assetList[0].value;
              this.name = this.assetList[0].label;
              if (this.searchName) {
                this.loadTableData();
              }
            }
          }

        }).finally(() => {

        });
      },
      scrollLeft() {
        this.pre = document.body.clientWidth / 1920;
        if (this.marginLeft !== 0 * this.pre) {
          this.marginLeft = this.marginLeft + 983 * this.pre;
        } else {
          this.marginLeft = 0;
        }
      },
      loadTableData(clear) {
        console.log(1111,this.searchName)
        if (!this.timeRange) {
          this.$Message.info(this.$t_p('time_placeholder'));
          return;
        }
        if (!this.searchName) {
          this.$Message.info(this.$t_p('asset_placeholder'));
          return;
        }
        this.assetNumber = this.name;

        if (clear) {
          this.pageInfo.pageIndex = 1;
        }
        this.isQuerying = true;
        let d = new Date(
          moment(moment(this.timeRange[1])).format("YYYY"),
          moment(moment(this.timeRange)).format("M"),
          0
        );
        //延迟优化显示
        getReport({
          checkDate: moment(this.timeRange).format("YYYY-MM"),
          name: this.searchName
        })
          .then(res => {
            this.isQuerying = false;
            if (res.data != null) {
              this.cardName = res.data.cardName || this.$t_p('default_card_name');
              //
              let d = new Date(
                moment(moment(this.timeRange[1])).format("YYYY"),
                moment(moment(this.timeRange)).format("M"),
                0
              );
              let arr = [];
              for (let i = 1; i < this.num + 1; i++) {
                arr.push(i)
              }
              this.tableColumns = [...arr];
              this.tableData = (res.data.data || []).map(d => ({
                ...d
              }));
            }

          })
          .finally(() => {
            this.isQuerying = false;
          });
      },
      onPaginationChange(index) {
        this.pageInfo.pageIndex = index;
        this.loadTableData(false);
      },
      onPaginationSizeChange(size) {
        this.pageInfo.pageSize = size;
        this.loadTableData(true);
      }
    },
    //生命周期 - 创建完成（可以访问当前this实例）
    created() {
    },
    //生命周期 - 挂载完成（可以访问DOM元素）
    mounted() {
      this.getType()
    }
  };
</script>
<style lang="less">
  .subCol > ul > li {
    margin: 0 -18px;
    list-style: none;
    text-align: center;
    padding: 9px;
    // border-bottom: 1px solid #ccc;
    overflow-x: hidden;
  }

  .subCol > ul > li:last-child {
    border-bottom: none;
  }
</style>

<style lang='less' scoped>
  //@import url(); 引入公共css类
  @import "../../style/varient";

  .transition(@config) {
    -webkit-transition: @config;
    -moz-transition: @config;
    -ms-transition: @config;
    -o-transition: @config;
    transition: @config;
  }

  @pre: 0.75;
  @size-padding: 10px;
  .report-info {
    /*height: 100%;*/
    overflow-y: auto;
    padding: 0 20px 20px 20px;

    .table-filters {
      display: inline-block;

      .filter-item {
        display: inline-block;
        margin-right: 20px;

        .label {
          font-size: @size-font-normal;
          color: #1f1f1f;
          padding: 0 9px;
          max-height: 7px;
          min-width: 4em;
        }
      }
    }

    .op-select {
      width: 420px;
    }

    .top {
      /*padding-left: 20px;*/
      overflow: auto;
      // margin-bottom: 10px;
      height: 60px;

      .lbl-name {
        font-size: @size-font-lg;
        font-weight: bold;
        margin-right: 8px;
      }
    }

    .content {
      height: calc(~"100% - " 40px);
      padding: 10px;
      position: relative;
      /*padding-top: 20px;*/
      /*padding-bottom: 20px;*/
      // height: 100%;
      .title {
        position: relative;
        height: 40px;
        line-height: 40px;
        text-align: center;
        font-weight: bold;

        .title-right {
          font-size: 12px;
          font-weight: normal;
          position: absolute;
          line-height: 40px;
          right: 0;
          top: 0;

          .icon {
            margin-left: 20px;
            margin-right: 5px;
            display: inline-block;
            vertical-align: middle;

            width: 12px;
            height: 12px;
            background-size: 100% 100%;
          }

          .icon-check {
            background-image: url("../../../static/img/check.png");
          }

          .icon-del {
            background-image: url("../../../static/img/del.png");
          }

          .icon-remove {
            background-image: url("../../../static/img/remove.png");
          }

          .icon-warning {
            background-image: url("../../../static/img/warning.png");
          }
        }
      }

      .user-table {
        /*border:1px solid #d2d2d2;*/

        .ivu-table-row.even {
          background-color: #e5f5fe;
        }

        ul,
        li {
          list-style: none;
          margin: 0;
          padding: 0;
        }

        .box {
          width: 100%;
          background: white;
          // height: 200px;
          // overflow-y: scroll;
          .box-title {
            display: flex;
            border-top: 1px solid #e5e5e5;
            background: #f8f8f9;

            li {
              width: 180px;
              border-bottom: 1px solid #e5e5e5;
              text-align: center;
              height: 70px;
              line-height: 70px;
              font-weight: bold;
            }

            li.box-title-number {
              width: 106px;
              border-left: 1px solid #e5e5e5;
            }

            .li-box {
              position: relative;

              width: 1049px;
              overflow: hidden;
              border-left: 1px solid #e5e5e5;

              div {
                display: flex;
                .transition(all 0.3s ease-out);
                flex: 1;
              }

              .li-box-left {
                width: 8px;
                position: absolute;
                height: 40px;
                background: url("../../../static/img/left.png") no-repeat;
                top: 20px;
              }

              .li-box-right {
                width: 8px;
                position: absolute;
                height: 40px;
                background: url("../../../static/img/right.png") no-repeat;
                top: 20px;
                right: 0;
              }

              li {
                flex: 0 0 auto;
                border-right: 1px solid #e5e5e5;
                text-align: center;
                width: 70px;
              }

              li:first-child {
                // border-left: 1px solid #e5e5e5;
              }
            }
          }

          .box-body {
            border-left: 1px solid #e5e5e5;
            border-right: 1px solid #e5e5e5;
            width: 100%;
            overflow: auto;

            .box-content {
              display: flex;

              li {
                width: 180px;
                text-align: center;
                flex: 0 0 auto;

                .box-time-box {
                  li {
                    text-align: center;
                    height: 70px;
                    line-height: 70px;
                    border-right: 1px solid #e5e5e5;
                    border-left: 1px solid #e5e5e5;
                    border-bottom: 1px solid #e5e5e5;
                  }

                  li:last-child {
                    border-bottom: 1px solid #e5e5e5;
                  }
                }

                .box-time-box:last-child {
                }
              }

              li.box-check-box {
                .body-box {
                  .transition(all 0.3s ease-out);
                  display: flex;

                  li {
                    flex: 0 0 auto;
                    width: 70px;
                    height: 70px;
                    line-height: 70px;
                    border-bottom: 1px solid #e5e5e5;
                    border-right: 1px solid #e5e5e5;
                    background-size: 12px 12px;
                    background-position: center center;
                    background-repeat: no-repeat;
                  }
                }
              }

              li.box-content-number {
                width: 106px;
                border-bottom: 1px solid #e5e5e5;
              }
            }

            .box-body-list:nth-child(1) {
              height: 70px;
              line-height: 70px;
              font-weight: bold;

              .box-content {
                li {
                  border-right: 1px solid #e5e5e5;
                  border-top: 1px solid #e5e5e5;
                  border-bottom: 1px solid #e5e5e5;
                }

                li:nth-child(n + 4) {
                  width: 70px;
                }
              }
            }

            .box-body-list:nth-child(2n) {
              background: #f8f8f9;
            }

            .body-icon-check {
              background-image: url("../../../static/img/check.png");
            }

            .body-icon-del {
              background-image: url("../../../static/img/del.png");
            }

            .body-icon-remove {
              background-image: url("../../../static/img/remove.png");
            }

            .body-icon-warning {
              background-image: url("../../../static/img/warning.png");
            }
          }
        }
      }

      .pagination {
        /*position: absolute;
            left: 0;
            bottom: @size-padding;
            right: @size-padding;
            font-size: @size-font-lg;*/
        margin-top: 20px;
        overflow: auto;
        margin-bottom: 20px;
        text-align: right;
      }
    }

    .container {
      display: flex;

      .container-left {
        overflow-x: scroll;
        width: 468px;

        .table-left {
          display: flex;

          li {
            flex: 0 0 auto;
            text-align: center;
          }

          li.table-li-1 {
            width: 108px;
            border-bottom: 1px solid #e5e5e5;
            border-left: 1px solid #e5e5e5;
            border-right: 1px solid #e5e5e5;
          }

          li.table-li-2 {
            width: 200px;
            border-bottom: 1px solid #e5e5e5;
            border-right: 1px solid #e5e5e5;
            display: flex;
            align-items: center;
            justify-content: center;

            p {
              line-height: 30px;
            }
          }

          li.table-li-3 {
            width: 160px;
            border-bottom: 1px solid #e5e5e5;
            // border-right: 1px solid #e5e5e5;
            li {
              border-bottom: 1px solid #e5e5e5;
            }

            li:last-child {
              border-bottom: none;
            }
          }

          .box-time-box {
            li {
              height: 1rem;
              line-height: 1rem;
              border-right: 1px solid #e5e5e5;
            }
          }
        }

        .table-left:first-child {
          font-weight: bold;
          background: #f5f6fa;

          li {
            height: 1rem;
            line-height: 1rem;
            border-right: 1px solid #e5e5e5;
            border-top: 1px solid #e5e5e5;
          }
        }
      }

      .container-right {
        flex: 1;
        overflow-x: auto;

        .table-right {
          margin: 0;

          li {
            flex: 0 0 auto;
            text-align: center;
            // border-bottom: 1px solid #e5e5e5;
            .list-ul {
              display: flex;
              // height: 70px;
              li {
                flex: 0 0 auto;
                width: 1rem;
                height: 1rem;
                line-height: 1rem;
                border-bottom: 1px solid #e5e5e5;
                border-right: 1px solid #e5e5e5;
              }

              .body-icon-check {
                background: url("../../../static/img/check.png") no-repeat center center;
              }

              .body-icon-del {
                background: url("../../../static/img/del.png") no-repeat center center;
              }

              .body-icon-remove {
                background: url("../../../static/img/remove.png") no-repeat center center;
              }

              .body-icon-warning {
                background: url("../../../static/img/warning.png") no-repeat center center;
              }
            }
          }

          .list-2 {
            display: block;
          }
        }

        .table-right:first-child {
          background: #f5f6fa;
          display: flex;
          font-weight: bold;

          li {
            width: 1rem;
            height: 1rem;
            line-height: 1rem;
            border-right: 1px solid #e5e5e5;
            border-top: 1px solid #e5e5e5;
            border-bottom: 1px solid #e5e5e5;
          }
        }
      }

      .container-right::-webkit-scrollbar {
        /*滚动条整体样式*/
        width: 10px; /*高宽分别对应横竖滚动条的尺寸*/
        height: 8px;
      }

      .container-right::-webkit-scrollbar-thumb {
        /*滚动条里面小方块*/
        border-radius: 10px;
        -webkit-box-shadow: inset 0 0 5px rgba(0, 0, 0, 0.1);
        background: #535353;
      }

      .container-right::-webkit-scrollbar-track {
        /*滚动条里面轨道*/
        -webkit-box-shadow: inset 0 0 5px rgba(0, 0, 0, 0.1);
        border-radius: 10px;
        background: #ededed;
      }

      .container-left::-webkit-scrollbar {
        /*滚动条整体样式*/
        width: 10px; /*高宽分别对应横竖滚动条的尺寸*/
        height: 8px;
      }

      .container-left::-webkit-scrollbar-thumb {
        /*滚动条里面小方块*/
        border-radius: 0;
        -webkit-box-shadow: inset 0 0 5px rgba(255, 255, 255, 1);
        background: #535353;
      }

      .container-left::-webkit-scrollbar-track {
        /*滚动条里面轨道*/
        -webkit-box-shadow: inset 0 0 5px rgba(255, 255, 255, 1);
        border-radius: 10px;
        background: #ededed;
      }
    }
  }
</style>



