<template>
  <Select class="asset-select" :filterable="true" :value="value" filterable :placeholder="placeholder" @input="onSelectionChange">
<!--    <Option value="" key="-1">全部</Option>-->
    <Option v-for="item in options" :value="item.value" :key="item.value">{{ item.label }}</Option>
  </Select>
</template>

<script>

  import {Select} from 'iview'
  import {getAssetList} from '@/api/asset'

  export default {
    name: "asset-select",
    data() {
      return {
        options: [],
        optionMap: {},
        item:{}
      }
    },
    props: {
      value: String,
      placeholder: String,
      type: String
    },
    model: {
      prop: 'value',
      event: 'selection'
    },
    watch: {
      type() {
        this.loadTreeData();
      }
    },
    methods: {
      loadTreeData() {
        getAssetList({
          isDaycheck:1
        }).then((res) => {
          let optionMap = {};
          this.options = (res.data.result || []).map((d) => {
            optionMap[d.id] = d;
            return {
              value: d.id + '',
              label: `${d.assetName}`
            }
          });

         this.options.length>0? this.item = this.options[0] : this.item={};
         this.$emit('selec', this.item);
          // let arr = [
          //   {value:'',label:'全部'}
          // ];
          // this.options = [...arr,...this.options]
          this.optionMap = optionMap;
        })
      },
      onSelectionChange(v) {
        this.$emit('selection', v);

        this.$emit('on-selection-change', this.optionMap[v]);
      }
    },
    mounted() {
      this.loadTreeData()
    },
    components: {
      Select
    }
  }
</script>

<style scoped>

  .asset-select {
    color: #000;
  }
</style>



